import React, { Component } from 'react';
import { HashRouter, Match, Miss } from 'react-router';
import ReactGA from 'react-ga';
ReactGA.initialize(process.env.REACT_APP_GAID);
import DocumentMeta from 'react-document-meta';
import './App.css';

import {
  About,
  NotFound,
  Header,
  Footer,
  Works,
  Talks,
} from './containers';

function logPageView(pathname) {
  ReactGA.set({ page: pathname });
  ReactGA.pageview(pathname);
}

class App extends Component {
  render() {
    const meta = {
      title: "Takayuki Ito",
      description: "I'm Takayuki Ito, Hello!!",
      canonical: 'https://takayuki-ito.me',
      meta: {
        charset: 'utf-8',
        name: {
          keywords: 'takayuki,ito,cheekit,nri,d3,react'
        },
        property: {
          'og:title': "Takayuki Ito's web site",
          'og:type': "website",
          'og:description': "I'm Takayuki Ito, Hello!!",
          'og:image': "https://takayuki-ito.me/profile.jpg",
          'og:url': "https://takayuki-ito.me",
          'og:site_name': "Takayuki Ito's web site"
        }
      }
    };

    return (
      <HashRouter onChange={logPageView}>
        {({ router, location }) => (
          <div className="container">
            <DocumentMeta {...meta} />
            <Header location={location}/>
            <div className="content">
              <Miss component={NotFound} />
              <MatchWithGA pattern="/" exactly component={() => <About router={router} />}/>
              <MatchWithGA pattern="/works" component={() => <Works router={router} />}/>
              <MatchWithGA pattern="/talks" component={() => <Talks router={router} />}/>
            </div>
            <Footer />
          </div>
        )}
      </HashRouter>
    );
  }
}

const MatchWithGA = ({ component: Component, ...rest }) => (
  <Match {...rest} render={props => {
    if(process.env.NODE_ENV === 'production') {
      logPageView(props.pathname);
    }
    return <Component {...props}/>;
  }}/>
);

export default App;



